body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
	text-align: center;
	font-family: 'Open Sans', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

a {
	font-family: 'Poppins', sans-serif;
}
* {
	text-align: center;
}

form {
	padding: 20px;
	
}

label {
	padding-right: 3px;
	margin-top: 6px;
	text-align: right;
}

button {
	margin-bottom: 5px;
	font-family: 'Poppins', sans-serif;
}

* {
	text-align: left;
}

button {
	float: right;
	text-decoration: none;
	color: black;
	margin:5px;
}

img {
	float:left;
	margin: 3px;
	width: 128px;
}
text {
	float:right;
}

.title {
	font-size: 20px;
	font-weight: bold;
}

a:link {
	text-decoration: none;
	color: white;
}


